"use client";

import { useState } from "react";
import { useSearchParams } from "next/navigation";
import Image from "next/image";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { Upload, CheckCircle2, XCircle } from "lucide-react";

export default function ClaimPage() {
  const params = useSearchParams();
  const shopId = params.get("shopId");

  const [file, setFile] = useState(null);
  const [preview, setPreview] = useState(null);
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState(null);

  if (!shopId) {
    return (
      <div className="flex justify-center p-10 h-screen items-center bg-custom-gradient">
        <Card className="p-6 max-w-md text-center border-red-200">
          <p className="text-red-600 font-medium text-lg">
            Invalid QR â€” Missing shopId
          </p>
        </Card>
      </div>
    );
  }

  const handleFileChange = (e) => {
    const selected = e.target.files?.[0];
    if (!selected) return;

    setFile(selected);
    setPreview(URL.createObjectURL(selected));
  };

  const handleSubmit = async () => {
    if (!file) return alert("Please select a screenshot");

    setLoading(true);
    setResult(null);

    const form = new FormData();
    form.append("file", file);
    form.append("shopId", shopId);

    const res = await fetch("/api/verify", {
      method: "POST",
      body: form,
    });

    const data = await res.json();
    setLoading(false);

    if (data.success) {
      setResult({ success: true });
    } else {
      setResult({ success: false, error: data.error || data.rejectReason });
    }
  };

  return (
    <div className="flex justify-center py-10 px-4">
      <Card className="w-full max-w-md shadow-xl border rounded-2xl">
        <CardHeader className="text-center bg-gradient-to-br from-indigo-500 to-purple-600 text-white rounded-t-2xl pb-6">
          <CardTitle className="text-xl font-semibold">
            Claim Your Stamp
          </CardTitle>
          <p className="text-white/90 text-sm">
            Upload your UPI payment screenshot
          </p>
        </CardHeader>

        <CardContent className="flex flex-col gap-6 py-6">

          {/* Image Preview OR Placeholder */}
          <div className="flex justify-center">
            {preview ? (
              <div className="rounded-xl overflow-hidden shadow-lg border">
                <Image
                  src={preview}
                  alt="preview"
                  width={280}
                  height={300}
                  className="object-cover"
                />
              </div>
            ) : (
              <div className="flex flex-col items-center gap-2 p-6 border-2 border-dashed rounded-xl text-gray-500 w-full text-center">
                <Upload className="w-8 h-8 opacity-70" />
                <p className="text-sm">Upload Screenshot</p>
                <p className="text-xs text-gray-400">PNG / JPG / JPEG</p>
              </div>
            )}
          </div>

          {/* File input */}
          <label className="flex flex-col gap-2">
            <p className="text-sm font-medium">Select Screenshot</p>
            <input
              type="file"
              accept="image/*"
              onChange={handleFileChange}
              className="border p-2 rounded-md w-full"
            />
          </label>

          {/* Submit Button */}
          <Button
            onClick={handleSubmit}
            disabled={loading || !file}
            className="w-full py-2 text-md font-medium"
          >
            {loading ? "Verifying..." : "Submit Screenshot"}
          </Button>

          {/* Result Messages */}
          {result?.success && (
            <Alert className="bg-green-50 border-green-300">
              <CheckCircle2 className="h-5 w-5 text-green-700" />
              <AlertTitle className="text-green-700 font-semibold">
                Payment Verified!
              </AlertTitle>
              <AlertDescription className="text-green-600">
                Stamp added successfully ðŸŽ‰
              </AlertDescription>
            </Alert>
          )}

          {result?.error && (
            <Alert variant="destructive">
              <XCircle className="h-5 w-5" />
              <AlertTitle className="font-semibold">Verification Failed</AlertTitle>
              <AlertDescription>{result.error}</AlertDescription>
            </Alert>
          )}
        </CardContent>
      </Card>
    </div>
  );
}
